.subscrie-form {
    padding: 4rem 8rem;
    border-radius: 0.5rem;
    background: url('../img/subs-bg.jpg') 0 50% no-repeat;

    &__content {
        width: 100%;
        max-width: 62rem;
        text-align: center;

        p {
            margin: 0;
            padding-bottom: 1rem;
        }
    }
    &__title {
        padding-bottom: 1rem;
        font-weight: 500;
        font-size: 3rem;
        font-family: $font-family-oswald;
        text-align: center;
        letter-spacing: 0.02em;
        text-transform: uppercase;
    }
    &__form {
        padding: 0.3rem;
        display: flex;
        flex-flow: row nowrap;
        align-items: center;
        border: 1px solid $color-font-10p;
        border-radius: 0.6rem;
        background-color: $color-white;

        input {
            padding: 1.5rem 2rem;
            margin: 0;
            border: 0;
            background: none;
            width: 100%;
        }
        button {
            width: 15rem;
        }
    }
}
